.carousel {
  margin: 5px auto;
  display: flex;
  align-items: center;
}

.slider-wrapper {
  flex: 1;
  overflow: hidden;
  position: relative;
}

.slides {
  display: flex;
  transition: transform 500ms ease;
  img {
    width: 100%;
  }
}

.slide {
  box-sizing: border-box;
  padding: 0 20px;
  img {
    width: 100%;
    height: 100%;
  }
}

/** Arrow styles */
.arrow {
  border-top: 2px solid;
  border-left: 2px solid;
  width: 20px;
  height: 20px;
  margin: 20px;
  color: rgba(0, 0, 0, 0.5);
}
.arrow:hover {
  color: rgba(0, 0, 0, 1);
}

.arrow-left {
  transform: rotate(315deg);
}

.arrow-right {
  transform: rotate(135deg);
}
